.search-dropdown {
  display: none;
  position: fixed;
  width: 100%;
  z-index: 2000;
  top: 74px;
  height: calc(100dvh - 74px);
  transition: $transition-default;
  @include screen(laptop) {
    top: 120px;
    &.scrolled {
      top: 80px;
    }
  }
  &.active {
    display: block;
  }
  .wrapper {
    padding: 0;
    @include screen(laptop) {
      padding: 0 #{$container-padding};
    }
  }
  &__wrapper {
    background-color: $white;
    overflow: hidden;
    padding: 0px 19px 0;
    border-radius: 20px;
    min-height: calc(100dvh - 74px);
    @include screen(laptop) {
      min-height: auto;
      box-shadow: 0px 0px 20px 0px #38364129;
      align-items: center;
      display: flex;
      gap: 50px;
      padding: 45px 60px 45px 90px;
    }
  }
  &__blocks {
    overflow-y: auto;
    max-height: calc(100dvh - 74px);
    @include screen(laptop) {
      display: grid;
      column-gap: 53px;
      overflow-y: inherit;
      width: 50%;
      row-gap: 58px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__block {
    border-top: 1px solid #DAD8E1;
    padding: 25px 0;
    @include screen(laptop) {
      border-top: none;
      grid-column: span 1 / span 1;
    }
    &-full {
      @include screen(laptop) {
        grid-column: span 2 / span 2;
      }
    }
    @include screen(laptop) {
      padding: 0;
    }
    h3 {
      font-family: $gotham;
      font-size: 15px;
      line-height: 15px;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 20px;
      }
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    margin-top: 20px;
    @include screen(laptop) {
      margin-top: 30px;
    }
  }
  &__link {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: flex-start;
    p {
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 24px;
      }
    }
    span {
      font-size: 12px;
      line-height: 16px;
      color: #817F87;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__brands {
    margin-top: 20px;
    gap: 12px;
    display: flex;
    overflow-x: auto;
    padding-bottom: 15px;
    @include screen(laptop) {
      margin-top: 30px;
      padding-bottom: 0;
      gap: 20px;
      overflow-x: inherit;
      display: grid;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__brand {
    padding: 16px;
    background-color: #F2F3F5;
    min-width: 82px;
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    @include screen(laptop) {
      padding: 20px 22px;
      border-radius: 24px;
    }
    img {
      object-fit: contain;
    }
  }
  &__slider {
    position: relative;
    width: 50%;
    display: none;
    @include screen(laptop) {
      display: block;
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-button-prev {
      left: -52px;
    }
    .swiper-button-next {
      right: -52px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: 24px;
      top: 50%;
      transform: translateY(-50%);
      height: 80px;
      display: none;
      border-radius: 8px;
      transition: background-color $transition-default;
      align-items: center;
      justify-content: center;
      @include screen(laptop) {
        display: flex;
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          background-color: #F2F3F5;
        }
      }
    }
    .card {
      &:hover {
        .card {
          &__image {
            @include screen(laptop) {
              &:before {
                height: 115px;
                border-radius: 24px;
                box-shadow: none;
                @include screen(laptop) {
                  border-radius: 42px;
                  height: 200px;
                  width: 80%;
                }
              }
            }
          }
          &__tags {
            @include screen(laptop) {
              left: 31px;
            }
          }
        }
      }
      .product__favorite {
        opacity: 1;
        display: flex;
        position: relative;
        top: auto;
        right: auto;
      }
      &__button {
        opacity: 1;
        margin-top: 0;
      }
      &__favorites {
        display: none;
      }
    }
  }
  &__more {
    display: inline-flex;
    left: 50%;
    transform: translateX(-50%);
  }
}