.recommended {
  padding: 50px 0;
  @include screen(laptop) {
    padding: 80px 0;
  }
  &__wrapper {
    position: relative;
    .swiper {
      margin-top: 20px;
      @include screen(laptop) {
        margin-top: 50px;
      }
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-button-prev {
      left: -52px;
    }
    .swiper-button-next {
      right: -52px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: 24px;
      top: 50%;
      transform: translateY(-50%);
      height: 80px;
      display: none;
      border-radius: 8px;
      transition: background-color $transition-default;
      align-items: center;
      justify-content: center;
      @include screen(laptop) {
        display: flex;
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          background-color: #F2F3F5;
        }
      }
    }
  }
  &__slide {
    display: flex;
    align-items: center;
    gap: 11px;
    padding: 18px 16px 20px;
    border-radius: 28px;
    flex-direction: column;
    background-color: #F2F3F5;
    @include screen(laptop) {
      padding: 25px 40px 25px 20px;
      border-radius: 36px;
      flex-direction: row;
    }
  }
  &__image {
    max-width: 80px;
    max-height: 89px;
    width: 100%;
    height: 100%;
    img {
      max-width: 80px;
      max-height: 89px;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__title {
    font-size: 14px;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 22px;
    }
  }
  &__footer {
    margin-top: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .button {
      width: 100%;
    }
  }
  &__price {
    display: none;
    @include screen(laptop) {
      display: block;
      font-family: $gotham;
      font-size: 18px;
      font-weight: 700;
      line-height: 17px;
    }
  }
}