.delivery {
  &-process {
    margin: 50px 0 0;
    background-color: #F2F3F5;
    border-radius: 50px;
    padding: 20px 25px;
    @include screen(laptop) {
      padding: 50px 75px;
      margin: 80px 0 40px;
    }
    h2 {
      font-family: $gotham;
      font-size: 16px;
      line-height: 20px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 10px;
      @include screen(laptop) {
        text-align: left;
        font-size: 24px;
        margin-bottom: 20px;
        line-height: 31px;
      }
    }
    p {
      font-size: 14px;
      line-height: 23px;
      margin-top: 10px;
      &:first-child {
        margin-top: 0;
      }
      @include screen(laptop) {
        font-size: 18px;
        line-height: 28px;
        margin-top: 0;
      }
    }
  }
  &-section {
    margin-top: 50px;
    @include screen(laptop) {
      margin-top: 80px;
    }
  }
  &-methods {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 15px;
    margin-top: 20px;
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 24px;
      margin-top: 40px;
    }
  }
  &-payments {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 15px;
    margin-top: 20px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 24px;
      margin-top: 32px;
    }
  }
  &__payment {
    border: 1px solid #DAD8E1;
    border-radius: 21px;
    padding: 25px 23px 26px;
    @include screen(laptop) {
      padding: 30px 50px;
    }
    h3 {
      font-size: 14px;
      font-family: $gotham;
      line-height: 19px;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 19px;
      }
    }
  }
  &__items {
    margin-top: 15px;
    display: flex;
    row-gap: 12px;
    column-gap: 10px;
    flex-wrap: wrap;
    @include screen(laptop) {
      gap: 15px;
      margin-top: 25px;
    }
  }
  &__item {
    padding: 8px 14px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    gap: 8px;
    border: 1px solid #DAD8E1;
    @include screen(laptop) {
      padding: 10px 18px;
    }
    img {
      max-height: 19px;
      width: 100%;
      object-fit: contain;
      @include screen(laptop) {
        max-height: 23px;
      }
    }
  }
  &__method {
    border: 1px solid #DAD8E1;
    border-radius: 21px;
    padding: 25px 21px 25px;
    @include screen(laptop) {
      padding: 30px 35px;
    }
    h2 {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-family: $gotham;
      font-size: 14px;
      line-height: 13px;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 19px;
      }
    }
    ul {
      margin-top: 15px;
      list-style: disc;
      margin-left: 20px;
      @include screen(laptop) {
        margin-top: 25px;
      }
      li {
        font-size: 14px;
        line-height: 23px;
        @include screen(laptop) {
          font-size: 18px;
          line-height: 28px;
        }
      }
    }
  }
  &__alert {
    display: inline-flex;
    gap: 23px;
    align-items: center;
    margin-top: 20px;
    background-color: #F2F3F5;
    padding: 20px 37px;
    border-radius: 21px;
    @include screen(laptop) {
      margin-top: 50px;
      padding: 30px 70px;
      gap: 15px;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }
    svg {
      min-width: 30px;
      min-height: 30px;
      @include screen(laptop) {
        min-width: 21px;
        min-height: 21px;
      }
    }
    span {
      font-family: $gotham;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      @include screen(laptop) {
        line-height: 19px;
        font-size: 20px;
      }
    }
  }
  &-banner {
    border-radius: 36px;
    margin-top: 28px;
    border-radius: 30px;
    overflow: hidden;
    position: relative;
    padding: 27px 32px;
    background-color: #FEACC2;
    @include screen(laptop) {
      padding: 46px 140px;
      border-radius: 36px;
      margin-top: 40px;
    }
    h2 {
      font-family: $gotham;
      z-index: 2;
      font-size: 16px;
      line-height: 19px;
      position: relative;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 38px;
        line-height: 36px;
      }
      span {
        padding: 12px 11px;
        border-radius: 16px;
        background-color: $text;
        font-size: 18px;
        line-height: 17px;
        display: inline-block;
        color: $white;
        @include screen(laptop) {
          font-size: 38px;
          padding: 17px 21px 10px;
          line-height: 36px;
        }
      }
    }
    p {
      margin-top: 18px;
      max-width: 160px;
      z-index: 2;
      font-size: 12px;
      line-height: 16px;
      position: relative;
      @include screen(laptop) {
        max-width: 561px;
        margin-top: 20px;
        font-size: 22px;
        line-height: 30px;
      }
    }
    img {
      position: absolute;
      right: 0;
      height: 100%;
      top: 0;
      object-fit: cover;
      transform: rotate(-45deg) translateX(30px) translateY(40px);
      @include screen(laptop) {
        transform: rotate(-25deg) scale(2) translateX(-80px) translateY(-50px);
      }
    }
  }
}