.product {
  padding: 35px 0 50px;
  overflow: hidden;
  @include screen(laptop) {
    padding: 70px 0 80px;
  }
  &__wrapper {
    @include screen(laptop) {
      column-gap: 24px;
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    @include screen(laptop) {
      grid-column: span 1 / span 1;
      grid-row: span 2 / span 2;
    }
  }
  &__facts {
    display: grid;
    margin-top: 40px;
    gap: 25px;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    @include screen(laptop) {
      margin-top: 55px;
      gap: 0;
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }
  }
  &__fact {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    @include screen(laptop) {
      gap: 15px;
    }
    img {
      min-width: 60px;
      min-height: 52px;
      max-width: 60px;
      max-height: 52px;
      @include screen(laptop) {
        min-width: 76px;
        min-height: 64px;
        max-width: 76px;
        max-height: 64px;
      }
    }
    span {
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      @include screen(laptop) {
        font-size: 14px;
        line-height: 19px;
      }
    }
  }
  &__colors {
    display: flex;
    align-items: center;
    margin-top: 25px;
    overflow-x: auto;
    gap: 12px;
    @include screen(laptop) {
      overflow-x: inherit;
      margin-top: 40px;
    }
  }
  &__color {
    input {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
      &:checked + img {
        padding: 2px;
        border: 2px solid #887DA7;
      }
    }
    img {
      padding: 2px;
      border: 2px solid transparent;
      border-radius: 50%;
      min-width: 36px;
      min-height: 36px;
    }
  }
  &__prices {
    margin-top: 20px;
    padding: 20px 0 30px;
    border-top: 1px solid #DAD8E1;
    border-bottom: 1px solid #DAD8E1;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    @include screen(laptop) {
      margin-top: 45px;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
      padding: 22px 0;
    }
    .button {
      margin-top: 14px;
      padding-left: 0;
      align-self: flex-start;
      @include screen(laptop) {
        align-self: inherit;
        margin-top: 0;
        padding: 0;
      }
      &-black {
        margin-top: 0;
        padding: 16px;
        width: 100%;
        @include screen(laptop) {
          padding: 14px 60px;
          width: auto;
        }
      }
    }
  }
  &__buttons {
    display: flex;
    align-items: center;
    width: 100%;
    flex-direction: column;
    @include screen(laptop) {
      gap: 20px;
      width: auto;
      flex-direction: row;
    }
  }
  &__costs {
    margin-bottom: 30px;
    margin-right: 0;
    @include screen(laptop) {
      margin-bottom: 0;
      margin-right: 0px;
    }
  }
  &__price {
    display: flex;
    align-items: center;
    gap: 15px;
    span {
      font-family: $gotham;
      font-size: 24px;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 31px;
      @include screen(laptop) {
        font-size: 32px;
        line-height: 41px;
      }
    }
  }
  &__oldprice {
    font-family: $gotham;
    color: #817F87;
    font-size: 16px;
    margin-top: 8px;
    text-decoration: line-through;
    line-height: 16px;
    @include screen(laptop) {
      margin-top: 5px;
      font-size: 18px;
      line-height: 18px;
    }
  }
  &__sale {
    font-size: 8px;
    line-height: 8px;
    font-family: $gotham;
    font-weight: 500;
    padding: 6px 8px;
    color: $white;
    background-color: #F6632A;
    border-radius: 6px;
    @include screen(laptop) {
      font-size: 10px;
      line-height: 10px;
    }
  }
  &__payment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-top: 25px;
    flex-wrap: wrap;
    @include screen(laptop) {
      margin-top: 30px;
    }
  }
  &__methods {
    display: flex;
    align-items: stretch;
    gap: 13px;
    @include screen(laptop) {
      gap: 15px;
    }
  }
  &__method {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #DAD8E1;
    border-radius: 9px;
    padding: 6px 11px;
    @include screen(laptop) {
      border-radius: 13px;
      padding: 7px 14px;
    }
    span {
      font-size: 12px;
      line-height: 16px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 22px;
      }
    }
    img {
      max-height: 16px;
      height: 16px;
      object-fit: contain;
      width: 100%;
      max-width: 54px;
    }
  }
  &__tip {
    display: flex;
    align-items: center;
    gap: 8px;
    span {
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }
  &__sizes {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 25px;
    @include screen(laptop) {
      gap: 10px;
      margin-top: 40px;
    }
  }
  &__size {
    span {
      border: 2px solid #DAD8E1;
      border-radius: 10px;
      padding: 7px 13px;
      font-size: 12px;
      line-height: 16px;
      display: inline-block;
      @include screen(laptop) {
        padding: 8px 12px;
        font-size: 16px;
        line-height: 22px;
      }
    }
    input {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
      &:checked + span {
        border: 2px solid #887DA7;
      }
    }
  }
  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 15px;
    @include screen(laptop) {
      gap: 20px;
      margin-top: 20px;
    }
  }
  &__top,
  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__left {
    @include screen(laptop) {
      grid-column: span 8 / span 8;
    }
  }
  &__right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    @include screen(laptop) {
      grid-column: span 4 / span 4;
    }
  }
  &__status {
    display: flex;
    align-items: center;
    gap: 8px;
    &-instock {
      span {
        color: #388361;
        font-size: 14px;
        line-height: 19px;
        @include screen(laptop) {
          font-size: 16px;
        }
      }
    }
  }
  &__brand {
    font-size: 15px;
    line-height: 20px;
    color: #545459;  
    margin-top: 18px;  
    @include screen(laptop) {
      font-size: 19px;
      line-height: 26px;
      margin-top: 0;
    }
  }
  &__sku {
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    margin-top: 15px;
    position: relative;
    color: #817F87;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 19px;
      position: static;
      margin-top: 20px;
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 15px;
    @include screen(laptop) {
      margin-top: 20px;
    }
    span {
      font-size: 14px;
      line-height: 19px;
      color: #817F87;
      @include screen(laptop) {
        font-size: 16px;
      }
    }
  }
  &__description {
    font-size: 14px;
    line-height: 21px;
    @include screen(laptop) {
      font-size: 19px;
      line-height: 29px;
    }
  }
  &__favorite {
    width: 42px;
    height: 42px;
    top: 0;
    border-radius: 13px;
    border: 1px solid #DAD8E1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    @include screen(laptop) {
      width: 46px;
      height: 46px;
    }
  }
  &__title {
    font-family: $gotham;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    max-width: 75%;
    @include screen(laptop) {
      font-size: 28px;
      line-height: 34px;
      max-width: 100%;
    }
  }
  &__slide {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__slider {
    @include screen(laptop) {
      grid-column: span 1 / span 1;
    }
    &-main {
      .swiper {
        max-width: 536px;
        overflow: visible;
      }
      .product__slide {
        position: relative;
        &:before {
          content: '';
          height: calc(100% - 60px);
          top: 0;
          width: 100%;
          background-color: #F3F2F9;
          border-radius: 42px;
          position: absolute;
        }
        img {
          position: relative;
          z-index: 2;
          margin-top: 20px;
          max-height: 435px;
          max-width: 435px;
          aspect-ratio: 1 / 1;
          object-fit: contain;
        }
      }
    }
    &-thumbs {
      position: relative;
      margin-top: 46px;
      display: none;
      @include screen(laptop) {
        display: block;
      }
      .swiper {
        max-width: 202px;
      }
      .product__slide {
        max-width: 61px;
        max-height: 61px;
        aspect-ratio: 1 / 1;
        border-radius: 17px;
        border: 2px solid #DAD8E1;
        padding: 6px;
        img {
          width: 100%;
          object-fit: contain;
          height: 100%;
        }
      }
      .swiper-slide-thumb {
        &-active {
          .product__slide {
            border-color: #887DA7;
          }
        }
      }
      .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0;
      }
      .swiper-button-prev {
        left: 140px;
      }
      .swiper-button-next {
        right: 140px;
      }
      .swiper-button-prev,
      .swiper-button-next {
        margin-top: 0;
        width: 33px;
        border-radius: 10px;
        transform: translateY(-50%);
        height: 33px;
        background-color: #F2F3F5;
        display: none;
        align-items: center;
        justify-content: center;
        @include screen(laptop) {
          display: flex;
        }
        &:after,
        &:before {
          display: none;
        }
        svg {
          height: 12px;
          path {
            @include screen(laptop) {
              transition: stroke $transition-default;
            }
          }
        }
        @include screen(laptop) {
          &:hover {
            svg {
              path {
                stroke: #C1BECA;
              }
            }
          }
        }
      }
    }
  }
  &__props {
    margin-top: 38px;
    @include screen(laptop) {
      margin-top: 60px;
      grid-column: span 1 / span 1;
    }
    &__list {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      align-items: center;
    }
    &__row {
      display: flex;
      margin-top: 20px;
      flex-direction: column;
      gap: 15px;
      @include screen(laptop) {
        gap: 0;
        flex-direction: row;
        margin-top: 30px;
      }
      span {
        margin-top: 6px;
        display: block;
        min-width: 125px;
        width: 100%;
        font-size: 14px;
        line-height: 19px;
        @include screen(laptop) {
          width: auto;
          display: inline-block;
          font-size: 16px;
          line-height: 22px;
        }
      }
    }
  }
  &__prop {
    padding: 8px 12px;
    border-radius: 10px;
    background-color: #F2F3F5;
    font-size: 12px;
    line-height: 20px;
    @include screen(laptop) {
      font-size: 15px;
      line-height: 20px;
    }
  }
  &__tags {
    position: absolute;
    left: -5px;
    top: 38px;
    z-index: 3;
    transition: $transition-default;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
}