.cart-popup {
  position: absolute;
  width: 626px;
  right: 0;
  display: none;
  top: 78px;
  transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
  z-index: 100;
  flex-direction: column;
  gap: 15px;
  @include screen(laptop) {
    display: flex;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }
  &__block,
  &__total {
    background-color: $white;
    border-radius: 12px;
    box-shadow: 0px 0px 20px 0px #38364129;
  }
  &__block {
    padding: 25px 23px 0;
  }
  &__total {
    padding: 20px 40px;
    a {
      margin-top: 20px;
      display: block;
      border-radius: 12px;
      background-color: $text;
      color: $white;
      text-align: center;
      padding: 20px;
      font-family: $gotham;
      font-size: 14px;
      line-height: 13px;
      text-transform: uppercase;
      font-weight: 500;
    }
  }
  &__image {
    position: relative;
    max-width: 93px;
    width: 100%;
    img {
      max-width: 93px;
      max-height: 113px;
      object-fit: contain;
      width: 100%;
      z-index: 2;
      position: relative;
    }
    &:before {
      transform: translateY(-50%);
      top: 50%;
      position: absolute;
      left: 0;
      content: '';
      border-radius: 17px;
      background-color: #F3F2F9;
      height: 68px;
      width: 93px;
    }
  }
  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h2 {
      font-family: $gotham;
      font-size: 24px;
      line-height: 31px;
      font-weight: 500;
    }
    h3 {
      font-family: $gotham;
      font-size: 24px;
      line-height: 31px;
    }
    strong {
      font-family: $gotham;
      font-size: 24px;
      line-height: 31px;
    }
  }
  &__products {
    margin-top: 25px;
    max-height: 372px;
    overflow-y: auto;
  }
  &__product {
    padding: 25px 20px;
    display: flex;
    align-items: center;
    gap: 30px;
    border-bottom: 1px solid #DAD8E1;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: none;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 10px;
  }
  &__top {
    position: relative;
    width: 100%;
    p {
      font-size: 16px;
      line-height: 20px;
      max-width: 260px;
    }
  }
  &__close {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    svg {
      rect {
        transition: fill $transition-default;
      }
    }
    @include screen(laptop) {
      &:hover {
        svg {
          rect {
            fill: $text;
          }
        }
      }
    }
  }
  &__bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
  }
  &__count {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
  }
  &__price {
    p {
      font-family: $gotham;
      font-size: 14px;
      text-align: right;
      color: #817F87;
      line-height: 13px;
      text-decoration: line-through;
    }
    span {
      display: inline-block;
      font-family: Gotham Pro;
      font-size: 18px;
      font-weight: 700;
      line-height: 17px;
      margin-top: 5px;
    }
  }
}