.promotion {
  &__banner {
    background-color: #FEEECD;
    padding: 23px 30px;
    border-radius: 36px;
    margin-top: 25px;
    @include screen(laptop) {
      padding: 50px;
      margin-top: 40px;
    }
  }
  &__container {
    max-width: 1146px;
    margin: 0 auto;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(laptop) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__heading {
    grid-column: span 2 / span 2;
    @include screen(laptop) {
      grid-column: span 7 / span 7;
    }
    h2 {
      font-family: $gotham;
      font-size: 14px;
      line-height: 18px;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 38px;
        line-height: 49px;
      }
    }
    p {
      font-size: 12px;
      line-height: 17px;
      margin-top: 10px;
      @include screen(laptop) {
        margin-top: 20px;
        font-size: 18px;
        line-height: 25px;
      }
    }
  }
  &__info {
    margin-top: 29px;
    margin-left: 24px;
    display: flex;
    flex-direction: column;
    grid-column: span 1 / span 1;
    align-items: flex-start;
    gap: 15px;
    @include screen(laptop) {
      grid-column: span 7 / span 7;
      margin-top: 52px;
      gap: 200px;
      margin-left: 0;
      align-items: center;
      flex-direction: row;
    }
  }
  &__date {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 10px;
    line-height: 13px;
    color: rgba($black, 0.8);
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__tip {
    font-size: 10px;
    line-height: 14px;
    color: rgba($black, 0.8);
    @include screen(laptop) {
      font-size: 16px;
      line-height: 22px;
    }
  }
  &__image {
    display: flex;
    justify-content: flex-end;
    grid-column: span 1 / span 1;
    @include screen(laptop) {
      grid-column: span 5 / span 5;
      margin-right: -60px;
      grid-row: span 2 / span 2;
    }
    img {
      max-width: 504px;
      width: 100%;
      max-height: 263px;
      object-fit: contain;
    }
  }
  &__content {
    max-width: 1146px;
    margin: 27px auto 0;
    @include screen(laptop) {
      margin: 60px auto 0;
    }
    p {
      font-size: 14px;
      line-height: 23px;
      margin-top: 20px;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 34px;
      }
      &.gray {
        opacity: 0.5;
      }
      &:first-child {
        margin-top: 0;
      }
    }
    ul {
      list-style: disc;
      margin-left: 24px;
      margin-top: 20px;
      li {
        font-size: 14px;
        line-height: 23px;
        @include screen(laptop) {
          font-size: 20px;
          line-height: 34px;
        }
      }
    }
    strong {
      font-size: 14px;
      line-height: 21px;
      display: inline-block;
      font-weight: 700;
      margin-top: 20px;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 34px;
      }
    }
  }
}