.contacts {
  &__container {
    padding: 25px 0 40px;
    border-top: 1px solid #DAD8E1;
    border-bottom: 1px solid #DAD8E1;
    margin-top: 40px;
    @include screen(laptop) {
      padding: 50px 0;
    }
  }
  &__image {
    margin-top: 30px;
    @include screen(laptop) {
      margin-top: 50px;
    }
    img {
      max-height: 194px;
      min-height: 194px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 30px;
      @include screen(laptop) {
        border-radius: 46px;
        max-height: 580px;
      }
    }
  }
  &__block {
    margin-top: 50px;
    display: grid;
    gap: 35px;
    &:first-child {
      margin-top: 0;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      column-gap: 200px;
    }
  }
  &__content {
    h2 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      @include screen(laptop) {
        font-size: 32px;
        line-height: 41px;
      }
    }
    p {
      margin-top: 15px;
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        margin-top: 25px;
        font-size: 18px;
        max-width: 352px;
        line-height: 28px;
      }
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 35px;
    align-items: flex-start;
    @include screen(laptop) {
      gap: 50px;
    }
  }
  &__link {
    display: inline-block;
    margin-top: 15px;
    border-bottom: 1px solid $text;
    font-weight: 600;
    font-size: 14px;
    line-height: 23px;
    @include screen(laptop) {
      font-size: 18px;
      margin-top: 20px;
      line-height: 28px;
    }
  }
  &__item {
    padding-left: 19px;
    @include screen(laptop) {
      padding-left: 39px;
    }
    h3 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 16px;
      line-height: 15px;
      position: relative;
      display: flex;
      gap: 6px;
      flex-wrap: wrap;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 19px;
      }
      &:before {
        content: '';
        position: absolute;
        left: -20px;
        top: 2px;
        width: 11px;
        height: 11px;
        border-radius: 3px;
        background-color: #887DA7;
      }
      a {
        font-family: $gotham;
        font-weight: 500;
        font-size: 16px;
        line-height: 15px;
        display: inline-block;
        @include screen(laptop) {
          font-size: 20px;
          line-height: 19px;
        }
      }
    }
    p {
      font-size: 14px;
      margin-top: 15px;
      line-height: 23px;
      a {
        font-weight: 600;
        border-bottom: 2px solid $text;
        padding-bottom: 2px;
      }
      @include screen(laptop) {
        font-size: 18px;
        margin-top: 20px;
        line-height: 28px;
      }
    }
  }
}