.blog {
  overflow: hidden;
  &__categories {
    margin-top: 35px;
    border-bottom: 1px solid #DAD8E1;
    padding-bottom: 24px;
    @include screen(laptop) {
      margin-top: 40px;
      padding-bottom: 60px;
    }
    .swiper {
      overflow: visible;
      @include screen(laptop) {
        overflow: hidden;
      }
      &-wrapper {
        align-items: stretch;
        @include screen(laptop) {
          align-items: flex-start;
        }
      }
      &-slide {
        height: auto;
        @include screen(laptop) {
          height: 100%;
        }
      }
    }
  }
  &-slide {
    position: relative;
    min-height: 215px;
    overflow: hidden;
    padding: 22px 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    border-radius: 24px;
    @include screen(laptop) {
      min-height: 302px;
      padding: 35px 44px;
      border-radius: 42px;
    }
    &:before {
      content: '';
      position: absolute;
      inset: 0;
      width: 100%;
      z-index: 2;
      height: 100%;
      background: linear-gradient(180deg, rgba(46, 44, 55, 0) -29.14%, rgba(46, 44, 55, 0.6) 79.94%);
    }
    &__bg {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }
    &__tag {
      z-index: 2;
      position: relative;
      display: inline-block;
      border-radius: 10px;
      padding: 7px 15px;
      background-color: #F2F3F5;
      font-size: 16px;
      line-height: 20px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 23px;
      }
    }
    &__content {
      margin-top: 72px;
      z-index: 2;
      width: 100%;
      position: relative;
    }
    &__title {
      font-family: $gotham;
      font-size: 16px;
      line-height: 19px;
      width: 60%;
      font-weight: 500;
      color: $white;
      padding-bottom: 15px;
      @include screen(laptop) {
        font-size: 28px;
        line-height: 33px;
      }
    }
    &__footer {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top: 1px solid rgba($white, 0.3);
      padding-top: 15px;
    }
    &__date {
      font-size: 12px;
      line-height: 16px;
      color: #D7D3D3;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    &__link {
      color: $white;
      display: flex;
      align-items: center;
      gap: 8px;
      span {
        text-transform: uppercase;
        color: $white;
        font-family: $gotham;
        font-size: 11px;
        font-weight: 500;
        margin-top: 2px;
        line-height: 10px;
        @include screen(laptop) {
          font-size: 14px;
          line-height: 13px;
        }
      }
    }
  }
  &__main {
    border-top: 1px solid #DAD8E1;
    border-bottom: 1px solid #DAD8E1;
    padding: 24px 0;
    position: relative;
    @include screen(laptop) {
      padding: 60px 0;
    }
    .swiper {
      overflow: visible;
      @include screen(laptop) {
        overflow: hidden;
      }
      &-wrapper {
        align-items: stretch;
      }
      &-slide {
        height: auto;
        border-radius: 24px;
        @include screen(laptop) {
          border-radius: 42px;
        }
      }
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-button-prev {
      left: -52px;
    }
    .swiper-button-next {
      right: -52px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: 24px;
      top: 50%;
      transform: translateY(-50%);
      height: 80px;
      display: none;
      border-radius: 8px;
      transition: background-color $transition-default;
      align-items: center;
      justify-content: center;
      @include screen(laptop) {
        display: flex;
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          background-color: #F2F3F5;
        }
      }
    }
  }
  &__category {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 24px;
    height: 100%;
    min-height: 85px;
    position: relative;
    overflow: hidden;
    padding: 19px;
    justify-content: center;
    @include screen(laptop) {
      border-radius: 41px;
      min-height: 151px;
    }
    &:before {
      content: '';
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background-color: #2E2C378C;
    }
    img {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    h3 {
      position: relative;
      z-index: 3;
      padding-bottom: 10px;
      text-align: center;
      font-family: $gotham;
      color: $white;
      font-size: 20px;
      font-weight: 500;
      line-height: 26px;
      &:before {
        content: '';
        position: absolute;
        bottom: 0;
        width: 76px;
        left: 50%;
        transform: translateX(-50%);
        height: 1px;
        background-color: #DAD8E1;
      }
    }
    span {
      display: inline-block;
      padding-top: 10px;
      font-size: 16px;
      z-index: 3;
      line-height: 22px;
      text-align: center;
      color: rgba($white, 0.8);
    }
  }
}