.video-blog {
  &__items {
    display: flex;
    gap: 15px;
    margin-top: 32px;
    overflow-x: auto;
    @include screen(laptop) {
      justify-content: center;
      gap: 29px;
      overflow-x: inherit;
    }
  }
  &__item {
    min-width: 74px;
    max-width: 74px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    width: 100%;
    height: 100%;
    @include screen(laptop) {
      min-width: 78px;
      max-width: 78px;
    }
    img {
      padding: 5px;
      border-radius: 20px;
      min-width: 74px;
      max-width: 74px;
      object-fit: cover;
      border: 2px solid #B5ABD0;
      @include screen(laptop) {
        min-width: 78px;
        border-radius: 25px;
        max-width: 78px;
      }
    }
    span {
      text-align: center;
      display: inline-block;
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        line-height: 17px;
        font-size: 13px;
        white-space: nowrap;
      }
    }
  }
}