.promotions {
  &__items {
    display: flex;
    align-items: stretch;
    gap: 16px;
    overflow-x: auto;
    padding-bottom: 20px;
    @include screen(laptop) {
      overflow-x: inherit;
      display: grid;
      padding-bottom: 0;
      gap: 20px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
    .promotion__content {
      margin: 0;
    }
  }
  &__title {
    font-family: $gotham;
    font-weight: 500;
    z-index: 2;
    position: relative;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 22px;
      line-height: 26px;
    }
  }
  &__description {
    font-size: 12px;
    z-index: 2;
    position: relative;
    line-height: 16px;
    margin-top: 15px;
    max-width: 376px;
    @include screen(laptop) {
      margin-top: 12px;
      font-size: 13px;
      line-height: 18px;
    }
  }
  img {
    position: absolute;
    right: -32px;
    bottom: -19px;
    z-index: 2;
    max-width: 150px;
    @include screen(laptop) {
      position: relative;
      margin-top: 59px;
      max-width: 158px;
      width: 100%;
    }
  }
  &__footer  {
    display: flex;
    z-index: 2;
    position: relative;
    align-items: flex-start;
    gap: 16px;
    justify-content: space-between;
    margin-top: 54px;
    flex-direction: column;
    @include screen(laptop) {
      margin-top: 72px;
      align-items: center;
      flex-direction: row;
    }
  }
  &__item {
    min-width: 292px;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include screen(laptop) {
      border-radius: 46px;
      min-width: auto;
    }
    &-yellow {
      background-color: #FFF3DD;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        bottom: -6px;
        right: 0;
        transform: scale(0.3);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(0.8);
        }
      }
    }
    &-green {
      background-color: #EEF9DC;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        top: 0;
        left: 0;
        transform: scale(0.6);
        transform-origin: top left;
        @include screen(laptop) {
          transform: scale(0.8);
        }
      }
    }
    &-pink {
      background-color: #FEE7EA;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        bottom: -6px;
        right: 0;
        transform: scale(0.5);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(0.8);
        }
      }
    }
    &-lg {
      position: relative;
      padding: 30px;
      @include screen(laptop) {
        padding: 56px 65px 47px;
      }
      .promotions__title {
        @include screen(laptop) {
          font-size: 30px;
          line-height: 28px;
        }
      }
      img {
        @include screen(laptop) {
          max-width: 478px;
          right: auto;
          bottom: auto;
        }
      }
    }
    &-md {
      position: relative;
      padding: 30px;
      @include screen(laptop) {
        padding: 44px 55px 47px;
      }
      img {
        position: absolute;
        right: -32px;
        bottom: -30px;
        max-width: 150px;
        @include screen(laptop) {
          right: 0;
          bottom: 50%;
          max-width: 224px;
          transform: translateY(50%);
          margin-top: 0;
          width: auto;
        }
      }
      .promotions__description {
        max-width: 177px;
        @include screen(laptop) {
          max-width: 236px;
        }
      }
      .promotions__footer {
        @include screen(laptop) {
          margin-top: 116px;
        }
      }
    }
    &-sm.promotions__item-yellow {
      &:before {
        transform: scale(0.3);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(0.8);
          max-width: 350px;
          max-height: 350px;
        }
      }
    }
    &-sm {
      position: relative;
      padding: 30px;
      
      @include screen(laptop) {
        padding: 36px 36px 40px;
      }
      img {
        position: absolute;
        right: -32px;
        bottom: -19px;
        margin-top: 0;
        width: auto;
      }
      .promotions__footer {
        @include screen(laptop) {
          margin-top: 87px;
        }
      }
      .promotions__description {
        max-width: 181px;
      }
    }
    &:nth-child(1) {
      grid-column: span 2 / span 2;
      grid-row: span 2 / span 2;
    }
    &:nth-child(4) {
      grid-column: span 2 / span 2;
      grid-column-start: 3;
      grid-row-start: 2;
    }
  }
  &__link  {
    background-color: transparent;
    border-radius: 0;
    padding: 0;
    color: $text;
    display: inline-flex;
    gap: 6px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 9px;
    line-height: 8px;
    text-align: center;
    font-family: $gotham;
    svg {
      width: 6px;
      height: 6px;
    }
    @include screen(laptop) {
      line-height: 8px;
      border-radius: 9px;
      font-size: 9px;
      padding: 0;
    }
    &-black {
      @include screen(laptop) {
        background-color: $text;
        border-radius: 9px;
        color: $white;
        padding: 12px 19px;
      }
      svg {
        @include screen(laptop) {
          display: none;
        }
      }
    }
  }
  &__limit {
    font-size: 12px;
    line-height: 16px;
    @include screen(laptop) {
      line-height: 18px;
      font-size: 13px;
    }
  }
  &__texts {
    max-width: 1090px;
    margin: 55px auto 0;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 20px;
    @include screen(laptop) {
      row-gap: 40px;
      column-gap: 49px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      margin-top: 89px;
    }
  }
  &__text {
    font-size: 14px;
    grid-column: span 1 / span 1;
    line-height: 23px;
    @include screen(laptop) {
      font-size: 14px;
      line-height: 21px;
    }
    &-main {
      font-family: $gotham;
      font-size: 16px;
      line-height: 23px;
      grid-column: span 1 / span 1;
      padding-left: 20px;
      position: relative;
      @include screen(laptop) {
        grid-column: span 2 / span 2;
        font-size: 22px;
        line-height: 29px;
        max-width: 929px;
        padding-left: 0;
      }
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        left: 0;
        top: -32px;
        transform: scale(0.5);
        transform-origin: left;
        @include screen(laptop) {
          transform: scale(1);
          top: -21px;
          left: -30px;
        }
      }
    }
  }
  &__advantages {
    margin-top: 45px;
    display: grid;
    align-items: center;
    row-gap: 30px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(laptop) {
      margin-top: 67px;
      row-gap: 0;
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }
  }
  &__advantage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    gap: 17px;
    svg {
      width: 45px;
      height: 45px;
      @include screen(laptop) {
        width: 52px;
        height: 52px;
      }
    }
    &:after {
      content: '';
      position: absolute;
      right: 0;
      height: 28px;
      width: 1px;
      background-color: #DAD8E1;
    }
    &:nth-child(2n) {
      &:after {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
    span {
      font-size: 14px;
      line-height: 21px;
      color: #545459;
      text-align: center;
      @include screen(laptop) {
        font-size: 13px;
        line-height: 17px;
      }
    }
  }
}