.breadcrumbs {
  padding-top: 20px;
  @include screen(laptop) {
    padding-top: 25px;
  }
  &__list {
    display: flex;
    align-items: center;
    overflow-x: auto;
    padding-bottom: 5px;
    gap: 6px;
    @include screen(laptop) {
      overflow-x: inherit;
    }
    svg {
      min-width: 16px;
    }
    a {
      color: #817F87; 
      white-space: nowrap;
      display: flex;
      align-items: center;
      @include screen(laptop) {
        transition: color $transition-default;
        &:hover {
          color: $text;
        }
      }
    }
    span {
      white-space: nowrap;
    }
  }
}