.articles {
  @include screen(laptop) {
    margin-bottom: 40px;
  }
}
.article { 
  padding: 24px 0;
  border-bottom: 1px solid #DAD8E1;
  display: grid;
  gap: 20px;
  @include screen(laptop) {
    gap: 54px;
    padding: 60px 0;
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }
  &__image {
    @include screen(laptop) {
      grid-column: span 4 / span 4;
    }
    img {
      width: 100%;
      max-height: 240px;
      height: 100%;
      object-fit: cover;
      border-radius: 30px;
    }
  }
  &__content {
    @include screen(laptop) {
      grid-column: span 8 / span 8;
    }
    p {
      font-size: 14px;
      line-height: 23px;
      color: #545459;
      margin-top: 20px;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden; 
      max-width: 709px;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 31px;
      }
    }
  }
  &__title {
    font-family: $gotham;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__tag {
    margin-top: 12px;
    padding: 7px 12px;
    display: inline-block;
    background-color: #F2F3F5;
    border-radius: 10px;
    font-size: 14px;
    line-height: 18px;
    @include screen(laptop) {
      font-size: 18px;
      margin-top: 15px;
      padding: 7px 15px;
      line-height: 23px;
    }
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid rgba($white, 0.3);
    padding-top: 25px;
    @include screen(laptop) {
      padding-top: 20px;
    }
  }
  &__date {
    font-size: 12px;
    line-height: 16px;
    color: #817F87;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 8px;
    span {
      text-transform: uppercase;
      font-family: $gotham;
      font-size: 11px;
      font-weight: 500;
      margin-top: 2px;
      line-height: 10px;
      @include screen(laptop) {
        font-size: 14px;
        line-height: 13px;
      }
    }
  }
}