.footer {
  padding: 42px 0 30px;
  position: relative;
  margin-top: 50px;
  @include screen(laptop) {
    padding: 55px;
    margin-top: 64px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 40% 0;
    }
  }
  &__logo {
    text-align: center;
    max-width: 227px;
    width: 100%;
    img {
      width: 100%;
      mix-blend-mode: multiply;
    }
  }
  &__wrapper {
    position: relative;
    z-index: 2;
    display: grid;
    gap: 20px;
    padding-bottom: 123px;
    @include screen(laptop) {
      padding-bottom: 0;
      grid-template-columns: 4fr 4fr 4fr;
    }
  }
  &__col {
    &-centered {
      order: 1;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      gap: 32px;
      @include screen(laptop) {
        order: inherit;
      }
    }
    &-main {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    &-company {
      order: 2;
      @include screen(laptop) {
        order: inherit;
      }
    }
  }
  &__title {
    font-size: 15px;
    font-weight: 600;
    line-height: 20px;
    width: 100%;
    align-self: flex-start;
  }
  &__blocks {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
    }
  }
  &__block {
    display: flex;
    flex-direction: column;
    h6 {
      text-align: center;
      @include screen(laptop) {
        text-align: left;
      }
    }
    &-company {
      h6 {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    &-payment {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
  &__menu {
    margin-top: 18px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
  &__link {
    padding-bottom: 20px;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid rgba(#CAC7D7, 0.8);
    @include screen(laptop) {
      padding-bottom: 0;
      font-size: 14px;
      line-height: 19px;
      border: 0;
      width: auto;
      text-align: left;
      &:hover {
        -webkit-text-stroke: 0.5px black;
      }
    }
  }
  &__contacts {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    row-gap: 9px;
    max-width: 395px;
    bottom: 20px;
    @include screen(laptop) {
      position: static;
      max-width: 100%;
    }
    a {
      font-size: 16px;
      font-weight: 600;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 15px;
        line-height: 20px;
        padding: 0 12px;
      }
      &:first-child {
        border-right: 1px solid rgba($black, 0.2);
        padding-right: 12px;
      }
      &:nth-child(2) {
        padding-left: 12px;
      }
    }
    p {
      color: #545459;
      font-size: 12px;
      line-height: 18px;
      text-align: center;
    }
  }
  &__payments {
    gap: 6px;
    margin-top: 22px;
    display: none;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__payment {
    border: 1px solid #DAD8E1;
    width: 87px;
    height: 41px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    img {
      max-height: 26px;
      object-fit: contain;
      max-width: 50px;
    }
  }
  &__icons {
    display: flex;
    width: 100%;
    justify-content: center;
    order: 1;
    border-bottom: 1px solid #BEBBC6;
    padding-bottom: 20px;
    align-items: center;
    gap: 30px;
    @include screen(laptop) {
      border: 0;
      width: auto;
      order: inherit;
      padding-bottom: 0;
    }
    img {
      min-width: 19px;
      min-height: 19px;
      max-width: 19px;
      max-height: 19px;
      width: 100%;
      opacity: 0.4;
    }
  }
}
.copy {
  background-color: #D6D2E1;
  padding: 27px 0 99px;
  color: #7C7A82;
  @include screen(laptop) {
    padding: 12px 0;
    margin-bottom: 0;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include screen(laptop) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__payments {
    display: flex;
    width: 100%;
    margin-top: 20px;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #BEBBC6;
    gap: 30px;
    padding-bottom: 20px;
    order: 2;
    @include screen(laptop) {
      order: inherit;
      gap: 46px;
      width: auto;
      display: none;
      border: 0;
      padding-bottom: 0;
      margin-top: 0;
    }
    img {
      opacity: 0.4;
      mix-blend-mode: multiply;
      max-width: 55px;
      max-height: 32px;
    }
  }
  &__icons {
    display: flex;
    width: 100%;
    justify-content: center;
    order: 1;
    border-bottom: 1px solid #BEBBC6;
    padding-bottom: 20px;
    align-items: center;
    gap: 30px;
    @include screen(laptop) {
      border: 0;
      width: auto;
      display: none;
      order: inherit;
      padding-bottom: 0;
    }
    img {
      min-width: 24px;
      min-height: 24px;
      max-width: 24px;
      max-height: 24px;
      width: 100%;
      opacity: 0.4;
    }
  }
  &__link {
    font-size: 14px;
    order: 3;
    margin-top: 25px;
    @include screen(laptop) {
      order: inherit;
      font-size: 13px;
      line-height: 17px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      margin-top: 0;
      &:hover {
        -webkit-text-stroke: 0.5px black;
      }
    }
  }
  &__name {
    order: 4;
    margin-top: 20px;
    font-size: 14px;
    @include screen(laptop) {
      order: inherit;
      font-size: 13px;
      line-height: 17px;
      margin-top: 0;
    }
  }
  &__author {
    order: 5;
    margin-top: 20px;
    @include screen(laptop) {
      order: inherit;
      margin-top: 0;
      max-width: 56px;
    }
  }
}