.product-info {
  background-color: #F2F3F5;
  padding: 50px 0 40px;
  @include screen(laptop) {
    padding: 50px 0 60px;
  }
  &__buttons {
    display: none;
    align-items: center;
    justify-content: center;
    gap: 115px;
    @include screen(laptop) {
      display: flex;
    }
  }
  &__heading {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    gap: 22px;
    @include screen(laptop) {
      flex-direction: row;
    }
    h2 {
      font-family: $gotham;
      font-weight: 500;
      line-height: 20px;
      font-size: 16px;
      @include screen(laptop) {
        line-height: 36px;
        font-size: 28px;
      }
    }
    span {
      margin-top: 10px;
      color: #817F87;
      font-size: 12px;
      line-height: 19px;
      @include screen(laptop) {
        margin-top: 11px;
        font-size: 14px;
        line-height: 19px;
      }
    }
    .filters__current {
      background-color: $white;
      max-width: 256px;
      @include screen(laptop) {
        max-width: 100%;
      }
    }
    .filters__dropdown {
      border: none;
    }
  }
  &__questions {
    display: flex;
    flex-direction: column-reverse;
    gap: 30px;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 70px;
    }
  }
  &__reviews {
    display: flex;
    flex-direction: column-reverse;
    gap: 30px;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 70px;
    }
  }
  &__content {
    @include screen(laptop) {
      grid-column: span 8 / span 8;
    }
    .button {
      margin-top: 25px;
      @include screen(laptop) {
        margin-top: 50px;
        left: auto;
        position: static;
        transform: translateX(0);
      }
    }
  }
  &__question,
  &__review {
    padding: 20px 0;
    border-bottom: 1px solid #DAD8E1;
    &:nth-last-child(2) {
      @include screen(laptop) {
        padding-bottom: 0;
        border: none;
      }
    }
    @include screen(laptop) {
      padding: 35px 0;
    }
    h3 {
      font-family: $gotham;
      font-size: 16px;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 24px;
        line-height: 31px;
      }
    }
    time {
      margin-top: 10px;
      display: inline-block;
      font-size: 12px;
      line-height: 19px;
      color: #817F87;
      @include screen(laptop) {
        font-size: 14px;
        line-height: 19px;
      }
    }
    p {
      margin-top: 20px;
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 27px;
        margin-top: 25px;
      }
    }
  }
  &__answer {
    margin-left: 15px;
    padding: 15px 20px;
    margin-top: 20px;
    max-width: 592px;
    background-color: $white;
    border-radius: 10px;
    @include screen(laptop) {
      margin-left: 32px;
      margin-top: 25px;
      padding: 20px 25px;
    }
    h4 {
      font-size: 14px;
      line-height: 21px;
      font-family: $gotham;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 27px;
      }
    }
    p {
      margin-top: 12px;
      font-size: 14px;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 27px;
      }
    }
  }
  &__button {
    font-family: $gotham;
    font-size: 18px;
    line-height: 17px;
    position: relative;
    padding-bottom: 10px;
    font-weight: 500;
    @include screen(laptop) {
      font-size: 32px;
      line-height: 30px;
    }
    &-mobile {
      display: flex;
      padding: 15px 0;
      border-bottom: 1px solid #DAD8E1;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      &:before {
        display: none;
      }
      svg {
        transition: transform $transition-default;
        rect {
          transition: fill $transition-default;
        } 
        path {
          transition: stroke $transition-default;
        }
      }
      &.active {
        border-bottom: none;
        svg {
          transform: rotate(180deg);
          rect {
            fill: #887DA7;
          }
          path {
            stroke: $white;
          }
        }
      }
      @include screen(laptop) {
        display: none;
      }
    }
    &:before {
      content: '';
      width: 96px;
      height: 2px;
      background-color: #B5ABD0;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      transition: opacity $transition-default;
    }
    &.active {
      &:before {
        opacity: 1;
      }
    }
    &:hover {
      &:before {
        opacity: 1;
      }
    }
  }
  &__description {
    display: grid;
    gap: 10px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 80px;
    }
    p {
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 28px;
      }
    }
  }
  &__containers {
    margin-top: 40px;
    @include screen(laptop) {
      margin-top: 40px;
    }
  }
  &__container {
    display: none;
    &.active {
      display: block;
      border-bottom: 1px solid #DAD8E1;
      padding-bottom: 30px;
      @include screen(laptop) {
        border-bottom: none;
        padding-bottom: 0;
      }
    }
  }
  &__aside {
    margin-top: 15px;
    @include screen(laptop) {
      margin-top: 0;
      grid-column: span 4 / span 4;
    }
  }
  &__compound {
    display: grid;
    gap: 20px;
    @include screen(laptop) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 83px;
    }
    p {
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        grid-column: span 8 / span 8;
        font-size: 18px;
        line-height: 27px;
      }
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 20px;
      @include screen(laptop) {
        grid-column: span 4 / span 4;
      }
      li {
        display: flex;
        position: relative;
        align-items: baseline;
        justify-content: space-between;
        &:before {
          content: '';
          border-bottom: 1px dashed #DAD8E1;
          flex-grow: 1;
          order: 2;
          margin: 0 5px;
        }
        p {
          font-size: 14px;
          line-height: 22px;
          color: #817F87;
          order: 1;
          @include screen(laptop) {
            font-size: 18px;
            line-height: 28px;
          }
        }
        span {
          order: 2;
          font-size: 14px;
          line-height: 22px;
          @include screen(laptop) {
            font-size: 18px;
            line-height: 28px;
          }
        }
      }
    }
  }
  &__tip {
    margin-top: 20px;
    font-size: 12px;
    line-height: 16px;
    @include screen(laptop) {
      font-size: 14px;
      line-height: 19px;
    }
  }
  &__counters {
    display: inline-flex;
    flex-direction: column;
    padding-top: 19px;
    margin-top: 19px;
    border-top: 1px solid #DAD8E1;
    gap: 12px;
    align-items: flex-start;
  }
  &__counter {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__rating {
    width: 100%;
    background-color: $white;
    padding: 30px;
    border-radius: 32px;
    @include screen(laptop) {
      position: sticky;
      max-width: 496px;
      padding: 30px;
      top: 200px;
    }
    .button {
      width: 100%;
      margin-top: 20px;
      justify-content: center;
      @include screen(laptop) {
        margin-top: 15px;
      }
    }
  }
  &__stats {
    display: flex;
    flex-direction: column;
    row-gap: 29px;
    column-gap: 32px;
    @include screen(laptop) {
      flex-wrap: wrap;
      flex-direction: row;
    }
  }
  &__stars {
    display: flex;
    gap: 4px;
    align-items: center;
  }
  &__stat {
    display: flex;
    align-items: center;
    gap: 13px;
    p {
      font-family: $gotham;
      font-size: 36px;
      line-height: 46px;
      font-weight: 500;
      color: #545163;
      @include screen(laptop) {
        font-size: 42px;
        line-height: 54px;
      }
    }
    span {
      font-size: 14px;
      color: #545163;
      line-height: 17px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
  &__ask {
    width: 100%;
    background-color: $white;
    padding: 30px;
    border-radius: 32px;
    @include screen(laptop) {
      position: sticky;
      max-width: 496px;
      padding: 30px;
      top: 200px;
    }
    h3 {
      display: flex;
      align-items: center;
      gap: 14px;
      font-weight: 500;
      font-family: $gotham;
      font-size: 18px;
      line-height: 17px;
      @include screen(laptop) {
        font-size: 28px;
        line-height: 26px;
      }
    }
    p {
      font-size: 12px;
      margin-top: 20px;
      line-height: 16px;
      color: #545459;
      @include screen(laptop) {
        font-size: 14px;
        line-height: 19px;
      }
    }
    .button {
      width: 100%;
      margin-top: 20px;
      justify-content: center;
      @include screen(laptop) {
        margin-top: 15px;
      }
    }
  }
}
.product-sticky {
  padding: 11px 0;
  position: fixed;
  width: 100%;
  bottom: 65px;
  border-top: 1px solid #DAD8E1;
  left: 0;
  z-index: 1000;
  background-color: $white;
  @include screen(laptop) {
    bottom: 0;
    padding: 23px 0 24px;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left,
  &__right {
    align-items: center;
  }
  &__left {
    display: none;
    position: relative;
    @include screen(laptop) {
      display: flex;
    }
  }
  &__title {
    margin-left: 94px;
    font-size: 18px;
    line-height: 23px;
  }
  &__image {
    position: absolute;
    left: 0;
    &:before {
      content: '';
      position: absolute;
      width: 77px;
      height: 57px;
      left: 50%;
      background-color: #F3F2F9;
      border-radius: 17px;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    img {
      position: relative;
      z-index: 2;
      max-width: 90px;
      max-height: 90px;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__right {
    width: 100%;
    display: flex;
    gap: 29px;
    justify-content: space-between;
    @include screen(laptop) {
      width: auto;
      gap: 35px;
    }
  }
  &__prices {
    display: flex;
    flex-direction: column;
    gap: 8px;
    @include screen(mobile-sm) {
      align-items: center;
      flex-direction: row;
      gap: 14px;
    }
    @include screen(laptop) {
      gap: 29px;
    }
  }
  &__price {
    font-family: $gotham;
    font-weight: 700;
    font-size: 18px;
    line-height: 17px;
    @include screen(laptop) {
      margin-left: 15px;
      line-height: 23px;
      font-size: 24px;
      margin-left: 0;
    }
  }
  &__oldprice {
    font-family: $gotham;
    text-decoration: line-through;
    color: #817F87;
    font-size: 12px;
    line-height: 11px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 15px;
    }
  }
  .button {
    padding: 15px 27px;
    margin-left: 0;
    @include screen(laptop) {
      padding: 16px 41px;
    }
  }
}