.post {
  overflow: hidden;
  .blog__main {
    border: none;
    padding-bottom: 35px;
    @include screen(laptop) {
      padding-bottom: 40px;
    }
    .title {
      display: none;
      @include screen(laptop) {
        margin-bottom: 40px;
        display: block;
      }
    }
  }
  &__container {
    padding-top: 30px;
    @include screen(laptop) {
      padding-top: 35px;
    }
  }
  &__info {
    margin: 0 auto;
    max-width: 1146px;
    row-gap: 26px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    margin-top: 20px;
    @include screen(laptop) {
      row-gap: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 40px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    @include screen(laptop) {
      justify-content: center;
      padding: 0 76px;
      border-right: 1px solid #DAD8E1;
    }
    &:nth-child(1) {
      padding-left: 0;
    }
    &:nth-child(3) {
      grid-column: span 2 / span 2;
      @include screen(mobile-lg) {
        grid-column: span 1 / span 1;
      }
    }
    &:last-child {
      padding-right: 0;
      border-right: none;
      grid-column: span 2 / span 2;
      @include screen(mobile-lg) {
        grid-column: span 1 / span 1;
      }
    }
    p {
      font-family: $gotham;
      font-size: 14px;
      line-height: 18px;
      text-align: center;
      background-color: #F2F3F5;
      border-radius: 10px;
      padding: 7px 12px;
      @include screen(laptop) {
        padding: 7px 15px;
        font-size: 18px;
        line-height: 23px;
      }
    }
    span {
      display: flex;
      align-items: center;
      color: #817F87;
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 24px;
      }
    }
    time {
      display: flex;
      gap: 5px;
      align-items: center;
    }
  }
  &__author {
    display: flex;
    align-items: center;
    svg {
      width: 46px;
      height: 46px;
      @include screen(laptop) {
        width: 68px;
        height: 68px;
      }
    }
  }
  &__name {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    margin-left: 19px;
    @include screen(laptop) {
      gap: 8px;
    }
    h4 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      @include screen(laptop) {
        line-height: 20px;
        font-size: 18px;
      }
    }
    span {
      font-size: 12px;
      line-height: 16px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__content {
    padding-bottom: 40px;
    position: relative;
    &:before {
      content: '';
      background-color: #F2F3F5;
      position: absolute;
      inset: 0;
      border-radius: 21px;
      width: 100%;
      height: calc(100% - 45px);
      top: 45px;
      @include screen(laptop) {
        height: calc(100% - 80px);
        top: 80px;
      }
    }
    @include screen(laptop) {
      padding-bottom: 70px;
    }
    &__wrapper {
      z-index: 2;
      position: relative;
      img {
        margin: 20px 0;
        border-radius: 36px;
        @include screen(laptop) {
          margin: 40px 0;
        }
      }
    }
  }
  &__block {
    margin: 0 auto;
    max-width: 1146px;
    ul {
      margin-top: 16px;
      @include screen(laptop) {
        margin-top: 25px;
      }
      li {
        font-size: 14px;
        line-height: 23px;
        @include screen(laptop) {
          font-size: 20px;
          line-height: 34px;
        }
      }
    }
    h3 {
      margin-top: 33px;
      font-size: 16px;
      line-height: 22px;
      font-family: $gotham;
      font-weight: 500;
      @include screen(laptop) {
        margin-top: 45px;
        font-size: 28px;
        line-height: 39px;
      }
    }
    h4 {
      margin-top: 17px;
      font-size: 14px;
      line-height: 23px;
      font-weight: 700;
      @include screen(laptop) {
        margin-top: 25px;
        font-size: 20px;
        line-height: 34px;
      }
    }
    p {
      margin-top: 16px;
      font-size: 14px;
      line-height: 23px;
      @include screen(laptop) {
        margin-top: 25px;
        font-size: 20px;
        line-height: 34px;
      }
    }
  }
}