.cart-empty {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 40px;
    gap: 25px;
    @include screen(laptop) {
      gap: 40px;
      padding-bottom: 60px;
    }
  }
  &__title {
    font-family: $gotham;
    font-size: 24px;
    line-height: 28px;
    font-weight: 500;
    text-align: center;
    @include screen(laptop) {
      font-family: $gotham;
      font-size: 38px;
      line-height: 36px;
    }
  }
  &__buttons {
    display: grid;
    align-items: center;
    gap: 15px;
    max-width: 543px;
    .button {
      white-space: nowrap;
      margin: 0;
    }
    @include screen(laptop) {
      flex-direction: row;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
}